.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.img-responsive {
  height: 300px;
}

.timeline>div {
  display: flex;
  flex-basis: calc(50% - 40px);
  justify-content: center;
  flex-direction: column;
  margin: 10px;
}
.red-border-left {
   border-left: 3px red solid;
}
.red-color-text {
  color: red !important;
}
.blue-color-text {
  color: blue !important;
}
.green-color-text {
  color: green !important;
}
.gray-color-text {
  color: gray !important;
}

.blue-border-left {
  border-left: 3px blue solid;
}

.green-border-left {
  border-left: 3px green solid;
}

.gray-border-left {
  border-left: 3px gray solid;
}

.visually-hidden{
  visibility: hidden;
}

.leaflet-container {
  background-color:rgba(255,0,0,0.0);
}